import uniLoadMore from '@dcloudio/uni-ui/lib/uni-load-more/uni-load-more.vue';
import tuiModal from '../../components/modal/modal'
export default {
  components:{
    uniLoadMore,
    tuiModal
  },
  data() {
    return {
      loadStatus: 'more',
      ruleShow:false,
      pullUpOn : true,
      loadding:false,
      navs:[
      ],
      active:0,
      params:{
        page:1,
        gc_id_3:'',
      },
      goodsLists:[],
    };
  },
  onLoad:function(){
    this.getClass();
    this.getGoods(true);
  },
  methods: {
    getClass(){
        this.$http({
          aurl:'groupClass',
          success:res => {
            this.navs = res.data.result
            this.navs.unshift({
              id:'',
              gc_name:'全部'
            })
          },
          fail:error => {
            console.log(error)
          }
        })
    },
    init(){
      this.loadStatus = 'more'
      this.pullUpOn = true
      this.loadding = false
      this.params.page = 1
      this.goodsLists = []
    },
    getGoods(init){
      if(init){
        this.init()
      }
      if (!this.pullUpOn) return;
      const params = this.params;
      this.$http({
        aurl:'groupGoods',
        params:params,
        success:res => {
          if(res.data.result.lists.data.length===0){
            this.loadding = false;
            this.pullUpOn = false;
            this.loadStatus = 'noMore';
          }else{
            for(let i = 0;i <res.data.result.lists.data.length;i++){
              this.goodsLists.push(res.data.result.lists.data[i])
            }
            this.params.page++;
            this.loadStatus = 'more';
            this.loadding = false;
          }
          if(init){
            if(res.data.result.lists.last_page===1){
              this.loadding = false;
              this.pullUpOn = false;
              this.loadStatus = 'noMore'
            }
          }
        },
        fail:error => {
          console.log(error)
        }
      })
    },
    navClick(item,index){
      this.active = index
      this.params.gc_id_3 = item.id
      this.getGoods(true)
    }
  },
  onReachBottom(){
    this.getGoods()
  },
  mounted() {
  },
};
